}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.collapseMenu {
  background: white;
  position: absolute;
  right: 0;
  top: 80px;
  padding: 20px;
  z-index: 10;
}

.collapseMenu ul {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.collapseMenu ul li a:hover {
  color: #c90008;
}

.collapseMenu ul li:hover {
  border-bottom: 1px solid lightgray;
}

@media screen and (min-width: 1001px) {
  .collapseMenu .nav_links {
    display: none;
  }
  .collapseMenu {
    display: none;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --primary: #c90008;
  --secondary: #282829;
}

nav {
  color: #c90008;
  color: var(--primary);
  background: white;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  position: fixed;
  padding: 30px 0;
  top: 0;
  width: 100%;
  z-index: 10;
  box-shadow: 5px 0 10px #282829;
  margin: auto;
}

@media screen and (max-width: 550px) {
  nav {
    display: flex;
    justify-content: space-between;
  }
}

@media screen and (max-width: 1100px) {
  nav {
    display: flex;
    justify-content: space-between;
  }
}

* {
