* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.menu_button {
  display: none;
  margin: 40px;
}

.menu_button div {
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 5px;
  transition: all 0.3s ease;
  pointer-events: none;
}

.toggle .menu_line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
.toggle .menu_line2 {
  opacity: 0;
}
.toggle .menu_line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

@media screen and (max-width: 1000px) {
  .menu_button {
    display: block;
  }
}

.nav_links {
  display: flex;
  justify-content: space-around;
  width: 40%;
  list-style: none;
}

.nav_link {
  padding: 10px;
}
.nav_link a {
  text-decoration: none;
  color: #282829;
  font-size: 20px;
  transition: all 0.5s ease;
}

.nav_link a:hover {
  color: #c90008;
  color: var(--primary);
}

.nav_links_open {
  transform: translateX(0%);
}

@media screen and (max-width: 1000px) {
  .nav_links {
    display: none;
  }
  .collapseMenu .nav_links {
    display: block;
  }
}

@media screen and (max-width: 1250px) {
  .nav_links {
    margin: 0 50px;
  }
}

.nav_logo_link img {
  font-size: 30px;
  font-weight: bolder;
  padding: 0 100px;
  color: #c90008;
  color: var(--primary);
  text-decoration: none;
  transition: all 0.5s ease;
}

.nav_logo_link img:hover {
  color: #282829;
  color: var(--secondary);
  scale: 1.1;
}

.home_button {
  font-size: 30px;
  font-weight: bolder;
  padding: 0 100px;
  color: #c90008;
  color: var(--primary);
  text-decoration: none;
  transition: all 0.5s ease;
  display: none;
}

.home_button:hover {
  color: #282829;
  scale: 1.05;
}

@media screen and (max-width: 550px) {
  .nav_logo_link {
    display: none;
  }
  .home_button {
    display: block;
  }
}

@media screen and (max-width: 1100px) {
  .nav_logo_link {
    padding: 0 50px;
    font-size: 25px;
  }

  .home_button {
    padding: 0 50px;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --primary: #c90008;
}

.landing1 {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.9)),
    url(/static/media/annie-spratt-bB0EsYazzXY-unsplash.e6672022.jpg);
  background-size: cover;
  background-position: center;
  font-family: "Roboto", sans-serif;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  display: block;
}

.landing1_content {
  width: 100%;
  position: relative;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -70%);
  color: #eee;
  text-align: center;
}

.landing1_img {
  width: 70vw;
  max-width: 600px;
  pointer-events: none;
  z-index: -1;
}
.landing1_content h1 {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-weight: lighter;
  font-size: 72px;
  letter-spacing: 2px;
}

.landing1_content p {
  font-family: "Roboto", sans-serif;
  font-size: 25px;
  color: lightgray;
  width: 50%;
  margin: auto;
  padding: 10px;
}

.landing1_links {
  display: flex;
  margin: auto;
  justify-content: space-around;
  width: 25%;
}

.landing1_links button {
  font-family: "Roboto", sans-serif;
  color: white;
  text-decoration: none;
  background: transparent;
  padding: 10px;
  margin: 20px auto;
  font-size: 20px;
  transition: all 0.5s ease;
  max-width: 250px;
}

.landing1_links a {
  font-family: "Roboto", sans-serif;
  color: white;
  text-decoration: none;
  background: transparent;
  padding: 10px;
  margin: 20px auto;
  font-size: 20px;
  transition: all 0.5s ease;
  max-width: 250px;
}

#learn_more {
  border: 2px solid #ffffff;
  color: #ffffff;
  font-weight: bold;
  letter-spacing: 1px;
  transition: all 0.5s ease;
  font-weight: lighter;
}

#learn_more:hover {
  background: rgba(201, 0, 8, 0.3);
  cursor: pointer;
}

#support_victory_kitchen {
  background-color: transparent;
  border: 1px solid white;
}

#support_victory_kitchen:hover {
  cursor: pointer;
  background: rgba(201, 0, 8, 0.3);
}

@media screen and (max-width: 1605px) {
  .landing1_links button {
    font-size: 15px;
  }
  .landing1_links a {
    font-size: 15px;
  }

  .landing1_links {
    flex-direction: column;
  }

  .landing1_content {
    top: 60%;
  }
}

@media screen and (max-width: 1620px) {
  .landing1_links button {
    font-size: 15px;
  }

  .landing1_links a {
    font-size: 15px;
  }

  .landing1_content p {
    width: 60%;
  }

  .landing1_links {
    flex-direction: column;
    font-size: 20px;
  }
  .landing1_links button {
    font-size: 20px;
  }
  .landing1_links a {
    font-size: 20px;
  }

  .landing1_content {
    top: 60%;
  }
}

@media screen and (max-width: 1050px) {
  .landing1_content p {
    width: 90%;
  }
}

@media screen and (max-width: 650px) {
  * {
    padding: 0;
    margin: 0;
  }

  .landing1_img {
    height: auto;
    width: 95vw;
  }

  .landing1 {
    width: 100%;
    max-height: 100vh;
  }

  .landing1_content {
    top: 55%;
  }

  .landing1_content h1 {
    font-size: 45px;
  }
  .landing1_content p {
    width: 100%;
    padding-top: 30px;
    padding-bottom: 30px;
    font-size: 20px;
  }
  .landing1_links {
    width: 100%;
    display: flex;
    align-items: center;
  }
  .landing1_links button {
    font-size: 15px;
    width: 200px;
  }
  .landing1_links a {
    font-size: 15px;
    width: 200px;
  }
}

.landing2 {
