}
.landing2_image img {
  max-height: 550px;
  width: auto;
}

.landing2 button {
  display: none;
}

@media screen and (max-width: 1100px) {
  .landing2 {
    background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.5),
