  }

  .landing2 h1 {
    color: white;
    margin-bottom: 15px;
  }

  .landing2 button {
    font-family: "Roboto", sans-serif;
    color: white;
    text-decoration: none;
    background: transparent;
    padding: 10px;
    margin: 20px auto;
    font-size: 20px;
    transition: all 0.5s ease;
    max-width: 250px;
    display: block;
  }

  .landing2 button:hover {
    cursor: pointer;
    background: rgba(201, 0, 8, 0.3);
    cursor: pointer;
  }
